html {
    height: 0;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    background-color: #EDEEEE;
    color: #424848;
}


.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    /* background-color: #DBD7C8; */
    background-color: #E9E7DE;
    width: 100%;
    height: auto;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color:#C47318;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #C47318;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color: inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

.required-field::before {
    content: "*";
    color: red;
}
